.advantages {
   font-family: "PT Sans", sans-serif;
}

.advantages__wrapper {
   width: 320px;
}

.advantages__list {
   padding: 0;
   margin: 0;
   list-style-type: none;
}

.advantages__item {}

.advantages__item:nth-of-type(4n+1) {
   color: #ffffff;
   background-color: #81B3D2;
}

.advantages__item:nth-of-type(n+6) {
   color: #000000;
   background-color: #EEEEEE;
}

.advantages__title {
   margin: 0;
   padding: 54px 18px 11px;
   font-style: normal;
   font-weight: 700;
   font-size: 18px;
   line-height: 26px;
   text-align: center;
   text-transform: uppercase;

}

.advantages__number {
   margin: 0;
   padding: 0;
   padding-bottom: 17px;
   font-style: normal;
   font-weight: 400;
   font-size: 21px;
   line-height: 10px;
   text-align: center;
   text-transform: uppercase;
}

.advantages__description {
   margin: 0;
   padding: 0 18px 40px;

   font-style: normal;
   font-weight: 400;
   font-size: 14px;
   line-height: 21px;
   text-align: center;
   text-transform: uppercase;

}

.advantages__content-image {
   margin: 0;
   padding: 0;
   background-position: 50% 50%;
   background-size: cover;
}

.advantages__icon {}

.advantages__title--advice {
   text-align: left;
}

.advantages__description--advice {
   text-align: left;
}
